<template>
  <div class="container"><br/><br/><br/>
    <div class="col-12">
      
      <h1>LOGIN</h1><br/>
      <!-- <div class="col-12r">
        <b-spinner v-show="progresso_ == true" variant="text-success" label=""></b-spinner>
        <b-alert v-show="alert_ == true" variant="warning">Usuário ou Senha Inválida!</b-alert>
      </div><br/>   
          -->
      <div class="col-19">
        <b-form-input v-model="usuario"  minlength="4" maxlength="60" placeholder="Usuário" ></b-form-input>
      </div><br/>

      <div class="col-12">
        <b-form-input v-model="senha"  minlength="4" maxlength="40" Type="password" placeholder="Senha" ></b-form-input>
      </div><br/><br/>

      <div class="col-12">
        <b-button variant="outline-primary" @click.prevent="submit_" >ENTER</b-button>
      </div><br/><br/> 

    </div>
  </div>
</template>


<script>
  import Vue from 'vue'
  import { FormGroupPlugin } from 'bootstrap-vue'
  import { InputGroupPlugin } from 'bootstrap-vue'
  import { BFormInput } from 'bootstrap-vue'
  import { BFormGroup } from 'bootstrap-vue'
  import { SpinnerPlugin } from 'bootstrap-vue'
  import { AlertPlugin } from 'bootstrap-vue'
  
  Vue.component('b-form-input', BFormInput);
  Vue.component('b-input-group', BFormGroup);
  Vue.use(AlertPlugin)
  Vue.use(SpinnerPlugin);
  Vue.use(InputGroupPlugin);
  Vue.use(FormGroupPlugin);
  
  export default {
    name:'loginComponent',
    
    data(){
      return {
        senha: "",
        usuario: "",
        alert_: false,
        progresso_: false,
      }
    },

    components:{
     //  Home - telas
    },
    
    mounted: function(){
      // //  montar
    },

    methods:{
      submit_(){
        this.alert_ = false
        if(this.senha == "")
          this.alert_ = true
        else if(this.usuario == "") 
          this.alert_ = true 
        else{
          this.progresso_ = true
          this.$router.push({name: 'home'});
        }     
      },
    }
  }

</script>

<style>

    body {
        background: black !important;
    }

    h1{
        color: #0d6efd ;
        border-color:red ;
    }

    #container_ {
        width: 100px;
        height: 100px;
    }

    .container{
      background:#fff;
      margin-top: 110px;
    }
   
</style>